import axios from 'axios'
import qs from 'qs'

// create an axios instance
const service = axios.create({
  // baseURL: 'https://health.mvheartcare.net',
  // baseURL: 'https://test.mvheartcare.net/',
  // baseURL: 'http://192.168.10.69/',
  baseURL: 'https://app.maillife.cn',
  timeout: 5000 // request timeout
})

// request interceptor
service.interceptors.request.use(
  config => {
    config.headers['distinction'] = 'web'
    if (config.method === 'post') {
      if (Object.prototype.toString.call(config.data) !== '[object FormData]') {
        const data = qs.parse(config.data)
        config.data = qs.stringify({
          ...data
        })
      }
    } else if (config.method === 'get') {
      config.params = {
        ...config.params
      }
    }
    return config
  },
  error => {
    console.log(error) // for debug
    return Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  /**
   * If you want to get http information such as headers or status
   * Please return  response => response
   */

  /**
   * Determine the request status by custom code
   * Here is just an example
   * You can also judge the status by HTTP Status Code
   */
  response => {
    const res = response.data
    // if the custom code is not 100, it is judged as an error.
    let code = parseInt(res.code)
    if (code !== 100) {

      if (code === 102) {
        console.log(res);
      }
      return res
    } else {
      return res
    }
  },
  error => {
    // alert('')
    console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)

export default service
